@import "helpers/mixins";
@import "helpers/helpers";
@import "helpers/fonts";
@import "helpers/variables";

.intro-text {
    padding-bottom: 42px;
    overflow: hidden;
    background-color: $grey-green;
    &.is-white {
        background-color: #fff; }
    @include desktop-less-lg {
        padding-bottom: 20px; }
    @include desktop-less-md {
        padding-bottom: 2px; }
    @include tablet-less-lg {
        padding-bottom: 12px; }
    @include mob-less-lg {
        padding-bottom: 42px; }
    &__in {
        font-size: 22px;
        line-height: 1.45;
        letter-spacing: -0.18px;
        margin-bottom: 62px;
        @include desktop-less-lg {
            margin-bottom: 60px; }
        h6 {
            margin-bottom: 14px;
            @include desktop-less-lg {
                line-height: 1.08;
                letter-spacing: -0.08px;
                margin-bottom: 12px; }
            @include desktop-less-md {
                margin-bottom: 14px; }
            @include desktop-less-sm {
                font-size: 24px; }
            @include tablet-less-lg {
                font-size: 22px; }
            @include mob-less-lg {
                font-size: 20px;
                line-height: 1.2;
                margin-bottom: 12px; } }

        p {
            margin-bottom: 18px;
            letter-spacing: 0px;
            @include hm-l;
            @include desktop-less-lg {
                line-height: 1.48;
                margin-bottom: 16px; }
            @include tablet-less-sm {
                font-size: 20px;
                line-height: 1.5; }
            @include mob-less-lg {
                margin-bottom: 10px; } }
        a {
            color: currentColor;
            text-decoration: none; }
        br {
            @include desktop-less-md {
                display: none; } }
        .button {
            margin-top: 13px;
            margin-right: 40px;
            &:last-of-type {
                margin-right: 0; }
            @include r(420) {
                margin-right: 10px; }

            @include desktop-less-lg {
                margin-top: 15px; }
            @include mob-less-lg {
                margin-top: 20px; } }
        &_sm {
            p {
                font-size: 16px;
                line-height: 1.5;
                @include desktop-less-lg {
                    line-height: 1.56; }
                @include tablet-less-sm {
                    font-size: 16px; } } } } }


